import React from 'react'
import { Link } from 'react-router'
const Header = ({ router }) => {
	return (
		<header className="col-md-12">
			<nav>
				<div className="row">
					<div className="col-md-8 col-sm-8 col-xs-4">
						<ul className="tabs">
							<li className="tab">
								<Link className="home-btn" to="/">
									<i className="fa fa-home" aria-hidden="true" />
								</Link>
							</li>
							<li className={`tab ${router.isActive('/resume') && 'active'}`}>
								<Link to="/resume">RESUME</Link>
							</li>
							<li className={`tab ${router.isActive('/skills') && 'active'}`}>
								<Link to="/skills">SKILLS</Link>
							</li>
							<li className={`tab ${router.isActive('/services') && 'active'}`}>
								<Link to="/services">SERVICES</Link>
							</li>
							<li className={`tab ${router.isActive('/portfolio') && 'active'}`}>
								<Link to="/portfolio">PORTFOLIO</Link>
							</li>
							<li className={`tab ${router.isActive('/contact') && 'active'}`}>
								<Link to="/contact">CONTACT</Link>
							</li>
						</ul>
					</div>
					<div className="col-md-4 col-sm-4 col-xs-8 dynamic">
						<a href="mailto:johncis.belista@gmail.com" className="pull-right site-btn icon hidden-xs">
							Contact Me <i className="fa fa-paper-plane" aria-hidden="true" />
						</a>
						<div className="hamburger pull-right hidden-lg hidden-md">
							<i className="fa fa-bars" aria-hidden="true" />
						</div>
						<div className="hidden-md social-icons pull-right">
							<a className="ins" href="//linkedin.com/in/johncisbelista">
								<i className="fa fa-linkedin" aria-hidden="true" />
							</a>
							<a className="tw" href="//github.com/johncisbelista">
								<i className="fa fa-github" aria-hidden="true" />
							</a>
							<a className="fb" href="//fb.me/iamjohncis">
								<i className="fa fa-facebook" aria-hidden="true" />
							</a>
						</div>
					</div>
				</div>
			</nav>
		</header>
	)
}

export default Header



