import React from 'react'
import { Row, Col } from 'react-bootstrap'
const Contact = props => {
	return (
		<div id="contact">
			<div className="row">
				<section className="contact-form col-md-6 padding_30 padbot_45">
					<div className="section-title top_15 bottom_30">
						<span />
						<h2>Send me a Message</h2>
					</div>
					<form className="site-form">
						<div className="row">
							<div className="col-md-6">
								<input className="site-input" placeholder="Name" />
							</div>
							<div className="col-md-6">
								<input className="site-input" placeholder="E-mail" />
							</div>
							<div className="col-md-12">
								<textarea className="site-area" placeholder="Message" />
							</div>
							<div className="col-md-12 top_15 bottom_30">
								<button className="site-btn" type="submit">
									Submit
								</button>
							</div>
						</div>
					</form>
				</section>
				<section className="contact-info col-md-6 padding_30 padbot_45">
					<div className="section-title top_15 bottom_30">
						<span />
						<h2>Contact Informations</h2>
					</div>
					<ul>
						<li>
							<span>Phone:</span> +63 905 443 0458
						</li>
						<li>
							<span>Job:</span> Front End Developer
						</li>
						<li>
							<span>E-mail:</span> johncis.belista@gmail.com
						</li>
						<li>
							<span>Skype:</span> johncis.belista
						</li>
						<li>
							<div className="social-icons top_15">
								<a className="in" href="//linkedin.com/in/johncisbelista">
									<i className="fa fa-linkedin" aria-hidden="true" />
								</a>
								<a className="gh" href="//github.com/johncisbelista">
									<i className="fa fa-github" aria-hidden="true" />
								</a>
								<a className="fb" href="//fb.me/iamjohncis">
									<i className="fa fa-facebook" aria-hidden="true" />
								</a>
							</div>
						</li>
					</ul>
				</section>
			</div>
		</div>
	)
}

export default Contact



