import { injectGlobal } from 'styled-components'
import '../styles/bootstrap.css'
import '../styles/font-awesome/css/font-awesome.min.css'
injectGlobal`
/*------------------------ 1 Body and Core Css ------------------------*/

.portfolio img.item {
  width: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
  background: #fafafa;
  overflow-x: hidden;
  position: relative;
}

.geometric-shapes {
  position: absolute;
  width: 100%;
}

html {
  height: 100%;
}

/* Typography */

hr {
  width: 100%;
  background: #efefef;
}

.blog-title {
  font-size: 26px;
  font-weight: bold;
  line-height: 30px;
}

.page-title i {
  font-size: 18px;
  color: #d2d2d2;
}

.timeline-title {
  font-size: 18px;
  font-weight: 500;
  margin-left: 25px;
  line-height: 30px;
}

.section-title {
  position: relative;
  display: inline-block;
  padding: 0 3px;
  h1, h2, h3, h4, h5 {
    font-size: 18px;
    font-weight: 600;
    position: relative;
  }
  span {
    position: absolute;
    height: 7px;
    width: 100%;
    bottom: 0;
    opacity: 0.4;
    left: 0;
  }
}

.site-btn {
  font-size: 14px;
  font-weight: 500;
  border: 0;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  padding: 1px 29px;
  line-height: 35px;
  &.black {
    background: #000 !important;
  }
}

.cbp-l-loadMore-link.site-btn {
  font-family: 'Roboto', sans-serif !important;
  font-size: 14px !important;
  font-weight: 500 !important;
  border: 0 !important;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  padding: 1px 29px !important;
  line-height: 35px !important;
}

.site-btn {
  &:hover {
    transition: all ease 0.3s;
  }
  &.icon {
    text-align: left;
    padding: 1px 6px 1px 19px;
    i {
      color: #000;
      background: #fff;
      margin-left: 15px;
      border-radius: 50px;
      font-size: 11px;
      height: 27px;
      width: 27px;
      float: right;
      text-align: center;
      line-height: 29px;
      margin-top: 4px;
    }
  }
}

#content {
  background: #fff;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
}

p {
  font-size: 13px;
  color: #4c4c4c;
  line-height: 24px;
}

.little-text {
  color: #989898;
  font-weight: 400;
  line-height: 18px;
}

blockquote {
  font-weight: 600;
  font-size: 18px;
  line-height: 30px;
}

h1 {
  font-size: 50px;
  font-weight: 500;
}

h2 {
  font-size: 43px;
  font-weight: 500;
}

h3 {
  font-size: 37px;
  font-weight: 500;
}

h4 {
  font-size: 32px;
  font-weight: 500;
}

h5 {
  font-size: 26px;
  font-weight: 500;
}

h6 {
  font-size: 20px;
  font-weight: 500;
}

/* Margins & Paddings */

.top_15 {
  margin-top: 15px;
}

.top_30 {
  margin-top: 30px;
}

.top_45 {
  margin-top: 45px;
}

.top_60 {
  margin-top: 60px;
}

.top_90 {
  margin-top: 90px;
}

.top_120 {
  margin-top: 120px;
}

.padding_30 {
  padding-top: 30px;
}

.padding_45 {
  padding-top: 45px;
}

.padding_50 {
  padding-top: 50px;
}

.padding_60 {
  padding-top: 60px;
}

.padding_90 {
  padding-top: 90px;
}

.bottom_15 {
  margin-bottom: 15px;
}

.bottom_30 {
  margin-bottom: 30px;
}

.bottom_45 {
  margin-bottom: 45px;
}

.bottom_60 {
  margin-bottom: 60px;
}

.bottom_90 {
  margin-bottom: 90px;
}

.bottom_120 {
  margin-bottom: 120px;
}

.padbot_15 {
  padding-bottom: 15px;
}

.padbot_30 {
  padding-bottom: 30px;
}

.padbot_45 {
  padding-bottom: 45px;
}

.padbot_50 {
  padding-bottom: 50px;
}

.padbot_60 {
  padding-bottom: 60px;
}

/*Spinner*/

#preloader {
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: table;
  text-align: center;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}


.spinner {
  &:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    margin-top: -30px;
    margin-left: -30px;
    border-radius: 50%;
    border: 1px solid #ccc;
    animation: spinner .6s linear infinite;
  }
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 50px;
    height: 50px;
    margin-top: -25px;
    margin-left: -25px;
    border-radius: 50px;
    background: url("/favicon.jpg") center center;
  }
}

/*------------------------ 2 Profile Section ------------------------*/

.profile {
  background: #fff;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  -o-border-radius: 6px;
  width: 100%;
  display: inline-block;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
}

.profile-name {
  line-height: 18px;
  padding: 20px;
}

.profile {
  .name {
    text-transform: uppercase;
    font-size: 17px;
    font-weight: bold;
  }
  .job {
    font-size: 13px;
    color: #777777;
  }
}

.profile-image {
  margin-top: -18px;
  -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
  clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
  img {
    width: 100%;
  }
}

ul.profile-information {
  list-style: none;
  margin: 8px 0px 8px 25px;
  li {
    border-left: solid 2px #eee;
    padding: 0 0 23px 20px;
    font-size: 14px;
    color: #787878;
    p {
      font-size: 13.5px;
      color: #7F7F7F;
      line-height: 12px;
    }
    &:last-child {
      border: 0;
      margin-left: 2px;
      margin-top: -2px;
    }
    &:before {
      content: "";
      border: solid 2px;
      width: 8px;
      height: 8px;
      border-radius: 50px;
      -moz-border-radius: 50px;
      -webkit-border-radius: 50px;
      font-size: 8px;
      margin-left: -25px;
      margin-top: 2px;
      font-weight: 400;
      background: #fff;
      display: block;
      position: absolute;
    }
    span {
      color: #000;
    }
    &:first-child {
      padding-bottom: 10px;
      padding-left: 30px;
      position: relative;
      img {
        margin: -44px -44px;
        display: inline-block;
        position: absolute;
        width: 25px;
      }
      &:before {
        /*content: '\f2c0';
        font-family: FontAwesome;
        font-size: 13.5px;*/
        margin-left: -51px;
        margin-top: -30px;
        margin-bottom: 20px;
        position: relative;
        border: 0;
        width: 40px;
        height: 40px;
        padding: 11px 14px;
        box-shadow: 0px 2px 32px 0px rgba(4, 123, 248, 0.3);
      }
      &:after {
        background: url("/images/glasses-w.png") no-repeat;
        content: '';
        width: 30px;
        height: 10px;
        position: absolute;
        top: 0;
        left: 0;
        margin: 16px -15px;
        background-size: 27px 10px;
      }
    }
  }
}

.profile .site-btn {
  margin: 18px auto 30px;
  display: block;
}

/*------------------------ 3 Navigation ------------------------*/

header nav {
  background: #fff;
  display: inline-block;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  height: 70px;
  width: 100%;
  margin-bottom: 30px;
  line-height: 70px;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  ul li {
    float: left;
    a {
      font-size: 14px;
      font-weight: bold;
      color: #000;
      padding: 0 10px;
      line-height: 73px;
      display: block;
      &:hover {
        transition: all ease 0.3s;
        -webkit-transition: all ease 0.3s;
        -moz-transition: all ease 0.3s;
      }
      &:after {
        border-right: 1px solid #dfdfdf;
      }
    }
  }
  a.home-btn {
    color: #fff;
    border-radius: 6px 0 0 6px;
    -webkit-border-radius: 6px 0 0 6px;
    -moz-border-radius: 6px 0 0 6px;
    padding: 0px 24px;
    float: left;
    height: 70px;
    line-height: 70px;
    margin-right: 10px;
    transition: all ease 0.4s;
    line-height: 77px;
    &:hover {
      transition: all ease 0.3s;
      -webkit-transition: all ease 0.3s;
      -moz-transition: all ease 0.3s;
    }
    i {
      font-size: 21px;
    }
  }
  .site-btn {
    margin: 18px 18px 0 5px;
  }
}

.social-icons a {
  color: #000;
  font-size: 12px;
  transition: all ease 0.2s;
  width: 24px;
  height: 18px;
  display: inline-block;
  &:hover {
    font-size: 14px;
  }
}

.fb:hover {
  color: #3b5998 !important;
  transition: all ease 0.2s;
}

.tw:hover {
  color: #55acee !important;
  transition: all ease 0.2s;
}

.ins:hover {
  color: #cd486b !important;
  transition: all ease 0.2s;
}

.bh:hover {
  color: #1769ff !important;
  transition: all ease 0.2s;
}

.dr:hover {
  color: #ea4c89 !important;
  transition: all ease 0.2s;
}

header nav .menuout {
  z-index: 10;
  display: none;
}

/*------------------------ 4 Home Section ------------------------*/

#content {
  min-height: 500px;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  padding: 15px;
}

section {
  padding-left: 30px !important;
  padding-right: 30px !important;
  &.line {
    border-bottom: solid 1px #efefef;
  }
  &:last-child {
    border: 0;
  }
  &.graybg {
    background: #fafafa;
  }
}

.services .service span.title {
  font-size: 14px;
  font-weight: 500;
  line-height: 30px;
}

.clients .client {
  background: #fff;
  padding: 18px;
  border-radius: 8px;
  box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
  width: 100%;
  padding: 26px 16px;
  &.bu{
  	padding:30px 16px;
  }
  &.mcs{
  	padding:16.5px 16px;
  }
  img {
    width: 100%;
  }
}

/*
.clients .col-md-3:nth-child(2) .client {
    padding: 23px 18px;
}*/

.service {
  .icon i:before {
    font-size: 44px;
    margin-left: 0;
  }
  background: #fff;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  padding: 20px;
  box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
  min-height: 186px;
}

/*------------------------ 5 Resume Section ------------------------*/

ul.timeline {
  margin-left: 10px;
}

.timeline li {
  border-left: solid 2px #ececec;
  list-style: none;
  padding: 0 0 40px 30px;
  &:before {
    content: "";
    border: solid 2px;
    width: 8px;
    height: 8px;
    border-radius: 50px;
    -moz-border-radius: 50px;
    -webkit-border-radius: 50px;
    font-size: 8px;
    margin-left: -35px;
    font-weight: 400;
    background: #fff;
    display: block;
    position: absolute;
    margin-top: -1px;
  }
  .line-title {
    font-size: 16px;
    line-height: 16px;
  }
  &:first-child {
    padding: 0 0 60px 19px;
    &:before {
      border: 0;
      background: none;
      position: relative;
    }
    i {
      float: left;
      margin-left: -30px;
      padding: 18px 16px;
      border-radius: 30px;
      margin: -13px -45px;
      background: #fff;
      width: 53px;
      text-align: center;
    }
  }
  .section-title {
    padding-left: 20px;
  }
  span {
    font-size: 12px;
    padding: 9px 0;
    display: block;
  }
  &:last-child {
    border: 0;
    margin-left: 2px;
  }
}

/* Skills */

.skill-list {
  li {
    list-style: none;
  }
  h3 {
    font-size: 12px;
  }
  .progress {
    margin: 10px 0 20px;
    display: block;
    height: 5px;
    position: relative;
    width: 100%;
    overflow: visible;
    border-radius: 50px;
    -moz-border-radius: 50px;
    -webkit-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    background: #dfdfdf;
    box-shadow: 0;
    .percentage {
      border-radius: 50px 0 0 50px;
      -moz-border-radius: 50px 0 0 50px;
      -webkit-border-radius: 50px 0 0 50px;
      -ms-border-radius: 50px 0 0 50px;
      -o-border-radius: 50px 0 0 50px;
      bottom: 0;
      position: absolute;
      top: 0;
    }
  }
}

/*------------------------ 6 Portfolio Section ------------------------*/

#portfolio {
  .cbp-item {
    padding: 15px !important;
  }
  section {
    padding-left: 15px !important;
    padding-right: 15px !important;
  }
  .cbp-item-wrapper {
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    display: inline-block;
    overflow: hidden;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    /*    transition: all ease 0.3s;
        -webkit-transition: all ease 0.3s;
        -moz-transition: all ease 0.3s;*/
  }
}

.content-header {
  z-index: 5;
}

#portfolio {
  .cbp-item-wrapper:hover {
    transition: all ease 0.3s;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.08);
    -webkit-box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.08);
    -moz-box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.08);
  }
  .cbp-item figure img {
    width: 100%;
  }
}

.work figure {
  width: 100%;
}

#portfolio {
  .cbp-item figcaption {
    .title {
      font-size: 14px;
      color: #000;
    }
    padding: 14px;
    .info {
      font-size: 13px;
      color: #989898;
      line-height: 18px;
      display: block;
    }
  }
  #filters-container .cbp-filter-item {
    font-size: 14px;
    color: #787878;
    cursor: pointer;
    float: left;
    margin-right: 15px;
  }
  .cbp-item {
    &:hover figure .icon {
      opacity: 0.9;
      transition: all ease 0.3s;
      -webkit-transition: all ease 0.3s;
      -moz-transition: all ease 0.3s;
      transform: translate(0, -25px);
      -webkit-transform: translate(0, -25px);
      -moz-transform: translate(0, -25px);
    }
    figure .icon {
      position: absolute;
      left: 0;
      right: 0;
      text-align: center;
      opacity: 0;
      display: table-cell;
      vertical-align: middle;
      top: 50%;
      margin-top: -20px;
      transition: all ease 0.3s;
      i {
        color: #fff;
        padding: 17px 0 16px;
        min-width: 50px;
        border-radius: 50px;
        text-align: center;
        transition: all ease 0.3s;
        text-align: center;
        background: rgba(3, 122, 249, 0.8);
        &:before {
          font-size: 18px;
        }
      }
    }
  }
}

/* portfolio content */

.portfolio-content figure img {
  width: 100%;
  border-radius: 5px;
}

.cbp-popup-singlePage {
  .cbp-popup-close {
    background: url("../images/sprite.png") -74px -3px no-repeat scroll transparent !important;
  }
  .cbp-popup-prev {
    background: url("../images/sprite.png") -23px -3px no-repeat scroll transparent !important;
  }
  .cbp-popup-next {
    background: url("../images/sprite.png") -127px -3px no-repeat scroll transparent !important;
  }
  .cbp-popup-close:hover {
    background: url("../images/sprite.png") -74px -46px no-repeat scroll transparent !important;
  }
  .cbp-popup-prev:hover {
    background: url("../images/sprite.png") -23px -46px no-repeat scroll transparent !important;
  }
  .cbp-popup-next:hover {
    background: url("../images/sprite.png") -127px -46px no-repeat scroll transparent !important;
  }
  .cbp-popup-navigation-wrap {
    background-color: #fff;
    box-shadow: -5px 10px 12px -10px rgba(201, 201, 201, 0.3);
  }
}

.cbp-popup-singlePage-counter {
  color: #c1c1c1;
}

.social-share ul {
  list-style: none;
  display: inline-block;
  li {
    list-style: none;
    float: left;
    margin-right: 10px;
    a {
      color: #b6b6b6;
      &:hover {
        color: #000;
        transition: all ease 0.3s;
        -webkit-transition: all ease 0.3s;
        -moz-transition: all ease 0.3s;
      }
    }
  }
}

.cbp-slider {
  .cbp-nav-next:after {
    width: 34px;
    height: 34px;
    background-size: 109px 140px;
    background-position: -35px -71px;
  }
  .cbp-nav-prev:after {
    width: 34px;
    height: 34px;
    background-size: 109px 140px;
    background-position: -35px -71px;
    background-position: 0px -71px;
  }
  .cbp-nav-next:after:hover, .cbp-nav-prev:after:hover {
    width: 34px;
    height: 34px;
    background-size: 109px 140px;
    background-position: -35px -71px;
    opacity: 0.3;
    transition: all ease 0.3s;
  }
  .cbp-nav-next:after {
    background-position: -35px -71px;
  }
  .cbp-nav-prev:hover:after {
    background-position: 0px -71px;
  }
  .cbp-nav-next:hover:after {
    background-position: -35px -71px;
  }
}

.mat-slider {
  border-radius: 6px;
  overflow: hidden;
}

.port-item {
  margin-bottom: 30px;
  display: block;
}

/*------------------------ 7 Blog Section ------------------------*/

#blog {
  .cbp-item {
    padding: 15px !important;
  }
  section {
    padding-left: 15px !important;
    padding-right: 15px !important;
  }
  a.blog-box {
    cursor: pointer;
    display: inline-block;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    overflow: hidden;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    transition: all ease 0.3s;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    &:hover {
      box-shadow: 0px 0px 8px 0px rgba(4, 6, 4, 0.08);
      transition: all ease 0.3s;
      -webkit-transition: all ease 0.3s;
      -moz-transition: all ease 0.3s;
    }
    .blog-img {
      position: relative;
    }
    .blog-box-info {
      padding: 15px;
      .little-text {
        padding: 5px 0;
      }
    }
  }
  .blog-box {
    .date span:last-child {
      opacity: 0.6;
    }
    span {
      &.category {
        font-size: 13px;
        color: #afafaf;
      }
      &.date {
        color: #505050 !important;
        font-size: 11px;
        font-family: arial;
      }
    }
    .title {
      font-size: 15px;
      color: #000;
      line-height: 20px;
      padding: 2px 0 4px;
    }
  }
}

/*Blog Detail Page */

#blog-post {
  padding: 30px 15px;
  .post {
    .blog-image {
      width: 100%;
      border-radius: 6px;
      -webkit-border-radius: 6px;
      -moz-border-radius: 6px;
      overflow: hidden;
      img {
        width: 100%;
      }
    }
    .blog-iframe iframe {
      width: 100%;
    }
    padding-right: 30px;
  }
  .blog-sidebar {
    border-left: solid 1px #efefef;
    padding-left: 30px;
    .category ul {
      list-style: none;
      li {
        font-size: 13px;
        color: #3d3d3d;
        border-bottom: solid 1px #efefef;
        line-height: 35px;
        a {
          color: #3d3d3d;
        }
        span {
          float: right;
          color: #8a8a8a;
        }
      }
    }
  }
  .post-list ul li {
    list-style: none;
    a {
      border-bottom: solid 1px #efefef;
      display: block;
      padding: 16px 0;
    }
    &:last-child a {
      border: 0;
    }
    a {
      .info {
        font-size: 12px;
        span {
          color: #b4b4b4;
          margin-left: 5px;
        }
      }
      p {
        font-size: 13px;
        line-height: 22px;
        color: #000;
      }
    }
  }
  .blog-sidebar .advertisement img {
    width: 100%;
  }
  .post {
    .tags {
      font-size: 12px;
      border-bottom: solid 1px #efefef;
      border-top: solid 1px #efefef;
      padding: 20px 0;
      span {
        margin-right: 7px;
      }
      a {
        color: #686969;
        margin-right: 3px;
      }
    }
    .post-list ul li a {
      p {
        font-size: 14px;
        padding-top: 5px;
      }
      padding: 25px 0;
    }
  }
  .post-comment {
    ul li {
      list-style: none;
      border-bottom: solid 1px #efefef;
      padding: 30px 0;
      &:last-child {
        border: 0;
      }
      .person-img {
        padding-right: 0;
        img {
          width: 100%;
        }
      }
      .comment-content {
        .name {
          font-size: 14px;
          font-weight: 500;
        }
        .date {
          font-size: 11px;
          color: #b0b0b0;
        }
        p {
          font-size: 13px;
          color: #989898;
          line-height: 20px;
          padding-top: 2px;
        }
        a {
          font-size: 12px;
          padding-top: 10px;
          display: block;
          i {
            margin-right: 5px;
          }
        }
      }
    }
    .reply {
      padding-left: 30px;
    }
  }
}

/* Sidebar */

/* Post Comments */

/* Form */

.site-form {
  .site-input {
    width: 100%;
    height: 45px;
    border: 0;
    color: #000;
    border-bottom: solid 1px #dfdfdf;
    font-size: 13px;
    padding-left: 10px;
    margin-bottom: 15px;
    outline: 0;
  }
  .site-area {
    width: 100%;
    border: 0;
    border-bottom: solid 1px #dfdfdf;
    font-size: 13px;
    padding: 20px 0 0 10px;
    min-height: 100px;
    margin-bottom: 15px;
    width: 100%;
    border: 0;
    border-bottom: solid 1px #dfdfdf;
    font-size: 13px;
    padding: 20px 0 0 10px;
    min-height: 100px;
    margin-bottom: 15px;
  }
}

/*------------------------ 8 Testimonial Section ------------------------*/

.testimonials {
  overflow: hidden;
  .comment {
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    width: 100%;
    padding: 15px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    .top-section {
      display: inline-block;
      figure {
        width: 23%;
        float: left;
        border-radius: 50px;
        overflow: hidden;
      }
      .name-info {
        width: 68%;
        padding: 8px 12px;
        float: left;
        line-height: 15px;
        .name {
          font-size: 14px;
          font-weight: 500;
        }
        .job {
          font-size: 12px;
          color: #d0d0d0;
        }
      }
      figure img {
        width: 100%;
      }
    }
    hr {
      width: 100%;
      margin: 6px 0px 10px;
    }
  }
  p {
    color: #989898;
    font-size: 12.5px;
    line-height: 20px;
  }
}

.owl-carousel .owl-wrapper-outer {
  overflow: inherit;
}

.owl-theme .owl-controls {
  .owl-page span {
    width: 10px;
    height: 10px;
    margin: 5px 5px;
  }
  margin-top: 45px;
}

/*------------------------ 9 Contact Section ------------------------*/

.contact-info ul li {
  list-style: none;
  font-size: 14px;
  color: #787878;
  line-height: 34px;
  span {
    color: #000;
  }
}

.contact-map iframe {
  width: 100%;
}

/*------------------------ 10 Footer ------------------------*/

footer .footer {
  background: #1e1e1e;
  min-height: 70px;
  color: #fff;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  line-height: 72px;
  .name {
    color: #fff;
    font-size: 15px;
  }
  .copyright {
    color: #9c9c9c;
    font-size: 12px;
    text-align: right;
    a {
      color: #fff;
      &:hover {
        color: #dfdfdf;
        transition: all ease 0.3s;
      }
    }
  }
}

/*------------------------ 11 Realtor Portfolio ------------------------*/

.featured-properties {
  overflow: hidden;
  .owl-carousel .item a {
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    display: inline-block;
    overflow: hidden;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
  }
  .estate figure figcaption {
    padding: 14px;
  }
}

.estate {
  figure {
    .imgout {
      position: relative;
    }
    img {
      width: 100%;
    }
    figcaption .info {
      font-size: 13px;
      color: #989898;
      line-height: 18px;
      display: block;
    }
  }
  figcaption .title {
    font-size: 14px;
    color: #000;
  }
  figure {
    .imgout {
      .type {
        font-size: 10px;
        position: absolute;
        top: 0;
        margin: 10px;
        color: #fff;
        padding: 4px 7px;
        background: #00000094;
        border-radius: 2px;
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
      }
      .price {
        font-size: 15px;
        position: absolute;
        bottom: 0;
        color: #fff;
        margin: 10px;
        padding: 3px 12px;
        border-radius: 2px;
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
      }
    }
    figcaption .features {
      font-size: 12.5px;
      border-top: solid 1px #dfdfdf;
      padding: 12px 0 0;
      color: #989898;
      margin-top: 8px;
      span {
        color: #000;
      }
    }
  }
}

/*Realtor Portfolio Detail */

.portfolio-content {
  .namenprice {
    display: inline-block;
    width: 100%;
  }
  .features {
    line-height: 34px;
    .info {
      font-size: 14px;
      color: #989898;
      line-height: 18px;
      display: block;
    }
    .room {
      font-size: 13px;
      color: #989898;
      span {
        color: #000;
        margin-left: 5px;
        &:first-child {
          margin-left: 0px;
        }
      }
    }
  }
}

.namenprice .price {
  font-size: 26px;
}

.details ul li {
  list-style: none;
  font-size: 13px;
  color: #989898;
  line-height: 25px;
  span {
    color: #000;
  }
}

.other-features.details ul li {
  color: #000;
  i {
    color: #52bf00;
  }
}

.map iframe {
  width: 100%;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  overflow: hidden;
  box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
}

/*------------------------ 12 Photo Gallery Widget ------------------------*/

#widget-gallery {
  margin-left: -15px;
  .cbp-item {
    img {
      width: 100%;
    }
    padding: 15px;
    a {
      display: block;
      border-radius: 5px;
      -moz-border-radius: 5px;
      -webkit-border-radius: 5px;
      overflow: hidden;
      box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
      -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
      -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
      &:hover {
        transition: all ease 0.3s;
        box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.2);
        -webkit-box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.2);
        -moz-box-shadow: 0px 0px 20px 0px rgba(4, 6, 4, 0.2);
      }
    }
  }
}

/*------------------------ 13 Table Widget  ------------------------*/

.table-title {
  font-size: 18px;
}

.mat-table {
  thead {
    font-size: 15px;
    font-weight: 500;
  }
  tbody {
    font-size: 15px;
  }
}

/*------------------------ 14 Pricing ------------------------*/

.mat-price {
  padding-bottom: 30px;
  ul {
    .site-btn {
      margin: 24px auto 0;
      display: block;
    }
    padding: 22px 20px 35px;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    li {
      font-size: 13px;
      list-style: none;
      text-align: center;
      line-height: 24px;
      &:first-child {
        font-size: 18px;
        font-weight: 600;
        border-bottom: 1px solid #dfdfdf;
        padding-bottom: 15px;
      }
      &:nth-child(2) {
        font-size: 24px;
        font-weight: 600;
        padding: 18px 0 15px;
      }
    }
  }
}

/*------------------------ 15 Events Widget ------------------------*/

.events {
  ul li {
    list-style: none;
    display: inline-block;
    width: 100%;
    .datenlocation {
      font-size: 12px;
      color: #c1c1c1;
    }
    .event-content {
      font-size: 17px;
      font-weight: 500;
    }
    .site-btn {
      float: right;
    }
  }
  .cbp-l-loadMore-link.site-btn {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
}

/*------------------------ 16 Discography Widget ------------------------*/

.discography {
  .album-cover {
    img {
      width: 100%;
      border-radius: 6px;
      -webkit-border-radius: 6px;
      -moz-border-radius: 6px;
      box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
      -webkit-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
      -moz-box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
    }
    .links {
      font-size: 12.5px;
      text-align: center;
      margin-top: 13px;
      color: #c5c5c5;
      a {
        color: #c5c5c5;
        margin-right: 3px;
        margin-left: 3px;
        &:hover {
          color: #949494;
          transition: all ease 0.3s;
        }
      }
    }
  }
  .album {
    font-size: 13px;
    ul li {
      &:first-child {
        font-size: 25px;
        font-weight: 500;
        line-height: 25px;
        padding-bottom: 18px;
        span {
          font-size: 13px;
          color: #b0b0b0;
          font-weight: 400;
        }
      }
      list-style: none;
      line-height: 28px;
    }
  }
}

/*------------------------ 17 Responsive ------------------------*/

@media (max-width: 1199px) {
  footer .footer .copyright {
    text-align: center;
  }
}

@media (max-width: 991px) {
  .wrapper.top_60 {
    margin-top: 30px;
  }
  .services .service {
    margin-bottom: 30px;
  }
  .code-skills {
    padding-top: 0;
  }
  #content {
    padding-bottom: 45px;
  }
  ul.profile-information {
    li:first-child {
      padding-bottom: 10px;
      padding-left: 30px;
    }
    list-style: none;
    margin: 0 auto 30px;
    display: table;
  }
  .profile {
    padding-bottom: 30px;
  }
  footer .footer .copyright {
    text-align: center;
  }
  .clients .client {
    margin-bottom: 30px;
  }
  .events ul li .site-btn {
    float: none;
    margin-top: 15px;
  }
}

@media screen and (max-width: 991px) and (min-width: 769px) {
  header nav {
    margin-top: 30px;
  }
  nav {
    .social-icons, .hamburger {
      display: none;
    }
  }
}

@media screen and (max-width: 991px) and (min-width: 576px) {
  .profile .name {
    font-size: 20px;
    margin-top: 11px;
  }
  .profile-image {
    margin-top: -18px;
    -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    margin-top: -40px;
  }
}

@media (max-width: 768px) {
  header {
    margin-top: 30px;
    nav {
      ul.tabs li:first-child, .menuout ul.tabs li {
        display: block;
      }
      ul.tabs li {
        display: none;
      }
      .menuout {
        width: 100%;
        position: relative;
        .menuin {
          width: 100%;
          background: #fff;
          position: absolute;
          z-index: 4;
          margin-top: 25px;
          border-radius: 8px;
          -moz-border-radius: 8px;
          -webkit-border-radius: 8px;
          box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.05);
          -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.05);
          -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.05);
          ul.tabs li {
            &:first-child {
              display: none;
            }
            float: none;
            text-align: center;
            border-bottom: solid 1px #efefef;
            a {
              width: 100%;
              display: block;
              cursor: pointer;
              line-height: 58px;
            }
          }
        }
      }
      .hamburger {
        font-size: 20px;
        margin-right: 23px;
        cursor: pointer;
        line-height: 71px;
        border-left: solid 1px #eaeaea;
        padding-left: 21px;
      }
      .menuout {
        display: block;
      }
    }
  }
  .social-icons {
    margin-right: 14px;
    line-height: 67px;
  }
  .portfolio-content .namenprice {
    .blog-title {
      width: 100%;
    }
    .price {
      float: left !important;
      margin-top: 14px;
    }
  }
}

@media (max-width: 321px) {
  .social-icons {
    display: none;
  }
  header nav .hamburger {
    border: 0;
  }
  footer .footer {
    line-height: 19px;
    padding-top: 14px;
  }
  .timeline li .line-title {
    line-height: 20px;
  }
}

/* --------------------------------- */
/* Css Color Style                   */
/*---------------------------------- */

header nav a.home-btn, .site-btn, .timeline li:first-child i, nav ul li a:after, .owl-theme .owl-controls .owl-page span, .skill-list .progress .percentage, ul.profile-information li:first-child:before {
  background: #425cbb !important;
}

header nav ul li a:hover, .cbp-filter-item-active, .portfolio-content p span {
  color: #425cbb !important;
}

#blog-post .blog-sidebar {
  ul.category-list li a:hover, .popular-posts ul li a .info {
    color: #425cbb !important;
  }
}

.spinner:before {
  border-top-color: #425cbb;
}

ul.profile-information li:before {
  border-color: #425cbb;
}

/* Hover Color*/

.site-btn:hover, header nav a.home-btn:hover, .section-title span {
  background-color: #536cca !important;
}

/* Opacity Color*/

#portfolio .cbp-item {
  .portfolio-image .icon i, figure .icon i {
    background: rgb(66, 91, 187);
  }
}

/* for dark colors */

.site-btn, header nav a.home-btn i, ul.profile-information li:first-child:before, #blog .blog-box .date span, .timeline li:first-child i, .cbp-l-loadMore-link.site-btn {
  color: #fff !important;
}

.timeline li:before, ul.profile-information li:before {
  border-color: #425cbb !important;
}

.tab.active a {
  color: #425cbb;
}

#blog-post {
  .post-list ul li a, .post-comment ul li .comment-content a {
    color: #425cbb;
  }
}

/* 
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com 
Twitter: @rich_clark
*/

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote {
  &:before, &:after {
    content: '';
    content: none;
  }
}

q {
  &:before, &:after {
    content: '';
    content: none;
  }
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none !important;
  &:focus {
    border: 0;
    outline: 0;
  }
}

button:focus {
  border: 0;
  outline: 0;
}

/* change colours to suit your needs */

ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */

mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

textarea:focus {
  border-color: rgba(0, 0, 0, 0.38) !important;
  outline: 0 none;
}

input {
  &[type="text"]:focus, &[type="password"]:focus, &[type="datetime"]:focus, &[type="datetime-local"]:focus, &[type="date"]:focus, &[type="month"]:focus, &[type="time"]:focus, &[type="week"]:focus, &[type="number"]:focus, &[type="email"]:focus, &[type="url"]:focus, &[type="search"]:focus, &[type="tel"]:focus, &[type="color"]:focus {
    border-color: rgba(0, 0, 0, 0.38) !important;
    outline: 0 none;
  }
}

.uneditable-input:focus {
  border-color: rgba(0, 0, 0, 0.38) !important;
  outline: 0 none;
}

.fade-out {
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden; 
}

pre {
	font-size: 13px;
    color: #4c4c4c;
    line-height: 24px;
    font-family: 'Roboto',sans-serif;
}
p {
    font-size: 13px;
    color: #4c4c4c;
    line-height: 24px;
    &.justified {
    	text-align:justified;
    }
}
`



